import Vue from 'vue';
import HTTP from 'vue-resource';
import PlayerInfo from './components/player-info.vue';
import _ from 'lodash';

Vue.config.debug = true;
Vue.use(HTTP);
Vue.http.headers.common['X-CSRF-TOKEN'] = App.csrfToken;


new Vue({

  el: 'body',

  components: {
    PlayerInfo
  },

  data: {
    players: [

    ],
    lastResults: '',
    results: []
  },

  methods: {

    play: function() {
      var postData = {
        player1: this.$data.player1.name,
        player2: this.$data.player2.name
      };

      this.$http.post('api/game/play', postData, function(results, status) {
        console.log(status, results);

        this.$data.lastResults = results;

        var winner = (results.winner == this.$data.player1.name)
          ? 'player1'
          : 'player2';

        this.$data[winner].wins.push(results);

        this.$data.results.push(results);
      }).error(function(status, data) {
        console.log(status, data);
      });
    },

    showGame: function(game) {
      // console.log(arguments);
      this.$http.get('api/game/' + game.hash, function(results, status) {
        console.log(status, results);
      });
    }

  }

});
